<div class="container search"><div class="search-box"><input class="search-box__input" type="text" v-model="search" @keydown.enter="getUserInfo" placeholder="Введите логин..."><img class="search-box__close" src="https://cdn.onlinewebfonts.com/svg/img_276651.png" alt="alt" v-if="search != ''" @click="clear"></div><button class="search__btn" @click="getUserInfo">НАЙТИ</button></div>