<div class="user-content" v-if="user"><div class="user-content-left"><img :src="user.avatar_url" alt="alt"><a :href="user.html_url">ПОСЕТИТЬ</a></div><div class="user-content-right"><h2>{{ user.login }}</h2><h3>Репозиториев: <span>{{ user.public_repos }}</span></h3><h4>Создан: <span>{{ user.created_at }}</span></h4><h5>Подписчиков: <span>{{ user.followers }}</span></h5><h6>Подписок: <span>{{ user.following }}</span></h6></div></div><div class="sort"><h2>Сортировка</h2><div class="sort-box"><button class="sort-box__btn" v-for="(btn, index) in btns" :key="btn.name" :class="{active: isActive == index}" @click="sort(index, btn.sortType)">{{ btn.name }}</button></div></div>